.App_container{
  padding: 0 1.1111111111111112em;
  padding-top: 1.1111111111111112em;

  width: 90%;
  margin: 0 auto;
}

.App_line{
  border: 3px solid #EFEFEF;
  margin: 1.25em 0;
}


/* No data found */
.noDataFound{
  /* border: 1px solid red; */
  padding: 1.25em;
  align-items: center;
  text-align: center;
}

.noDataFound_img{
  max-width: 225px;
  opacity: .7;
}

.noDataFound p{
  padding-left: 3.125em;
}


@media screen and (min-width: 1024px){
  body{
    font-size: 18px;
  }

  .App_container{
    width: 70%;
  }

  .app_main-grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    gap: 1rem;
  }

  .Card{
    width: 100%;
  }
}

@media screen and (min-width: 2560px){
  .App_container{
    width: 60%;
  }
}