.Navigation_logo {
  text-align: center;
  font-size: 3.577rem;
  font-weight: 900;
  padding-bottom: 0.6666666666666666em;
}

.Navigation_logo span {
  text-decoration: underline;
}

.Navigaiton_button {
  border: none;
  margin: 0 auto;
  padding: 0.313em 0.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
  background-color: #8E7DFF;
  color: white;
}

.Navigaiton_button:hover{
  background-color: #857ACC;
}

.Navigation_addIcon {
  fill: white;
  width: 20px;
  padding-left: 0.5em;
}